<template>
  <div class="container">
    <h1>Sikeres Regisztrci</h1>
    <h2>Kedves {{attendee.name}}!</h2>
    <div>Ksznjk a regisztrcit! </div>
    <div>Izgatottan vrjuk, hogy tallkozzunk az eskvn<span v-show="attendees.length>0"> s egytt mulassunk</span>. </div>
    <div>Az eskv dtuma 2021-09-04, vrunk sok szeretettel 14:30-tl a Talizmn vendgl kerthelyisgben. </div>
    <div v-show="attendee.attendeeAllergyCount>0"> 
        A kvetkez<span v-show="attendeeAllergyCount>1">k</span>re igyeksznk figyelni a kedvedrt:
        Nem fogyasztasz {{attendee.allergyText}}</div>

    <div v-show="attendees.length>0">
        <div class="my-padding-small">A kvetkez ltalad megadott plusz fkkel fogunk szmolni:</div>
        <div v-for="(attendee,k) in attendees" :key="k">
            <h6>{{attendee.name}}</h6>
            <div v-if="attendee.attendeeAllergyCount>0" class="my-padding-small"> 
                A kvetkez<span v-show="attendeeAllergyCount>1">k</span>re igyeksznk figyelni a kedvrt:
                Nem fogyaszt {{attendee.allergyText}}
            </div>
            <div class="my-padding-small" v-else>
            Nincs semmilyen kaja ignye
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
    name: 'RegistrationInfo',
    props: {
        attendee: {required: true},
        attendees:{required: true}
    },
    data() {
        return {
        }
    },
    created:function(){
        this.attendee.attendeeAllergyCount=0;
        this.attendee.allergyText=" ";
        if(this.attendee.vegan){
            this.attendee.attendeeAllergyCount++;
            this.attendee.allergyText+="llati eredet teleket, ";
        }
        if(this.attendee.lactose){
            this.attendee.attendeeAllergyCount++;
            this.attendee.allergyText+="laktzt, ";
        }
        if(this.attendee.milk){
            this.attendee.attendeeAllergyCount++;
            this.attendee.allergyText+="tejtermket, ";
        }
        if(this.attendee.gluten){
            this.attendee.attendeeAllergyCount++;
            this.attendee.allergyText+="glutnt, ";
        }
        if(this.attendee.other){
            this.attendee.attendeeAllergyCount++;
            this.attendee.allergyText+=this.attendee.other+", ";
        }
        this.attendee.allergyText=this.attendee.allergyText.slice(0,-2);
        this.attendee.allergyText+=".";
        for(let i = 0; i<this.attendees.length;i++){
            this.attendees[i].attendeeAllergyCount=0;
            this.attendees[i].allergyText=" ";
            if(this.attendees[i].vegan){
                this.attendees[i].attendeeAllergyCount++;
                this.attendees[i].allergyText+="llati eredet teleket, ";
            }
            if(this.attendees[i].lactose){
                this.attendees[i].attendeeAllergyCount++;
                this.attendees[i].allergyText+="laktzt, ";
            }
            if(this.attendees[i].milk){
                this.attendees[i].attendeeAllergyCount++;
                this.attendees[i].allergyText+="tejtermket, ";
            }
            if(this.attendees[i].gluten){
                this.attendees[i].attendeeAllergyCount++;
                this.attendees[i].allergyText+="glutnt, ";
            }
            if(this.attendees[i].other){
                this.attendees[i].attendeeAllergyCount++;
                this.attendees[i].allergyText+=this.attendees[i].other+", ";
            }
            this.attendees[i].allergyText=this.attendees[i].allergyText.slice(0,-2);
            this.attendees[i].allergyText+=".";

        }

        let message="Sikeres Regisztrci!\n"+
            "Kedves "+ this.attendee.name+"!\n"+
            "Ksznjk a regisztrcit!\n"+
            "Izgatottan vrjuk, hogy tallkozzunk az eskvn ";
        if(this.attendees&&this.attendees.length>0) {
            message += "s egytt mulassunk.";
        }
        message+="\nAz eskv dtuma 2021-09-04, vrunk sok szeretettel 14:30-tl a Talizmn vendgl kerthelyisgben.\n";
        if(this.attendee.attendeeAllergyCount>0){
            message+="A kvetkez";
            if(this.attendee.attendeeAllergyCount>1){
                message+="k";
            }
            message+="re igyeksznk figyelni a kedvedrt:\n";
            message+="Nem fogyasztasz"+this.attendee.allergyText;
        }
        if(this.attendees && this.attendees.length>0){
            message+="\n\n\nA kvetkez ltalad megadott plusz fkkel fogunk szmolni:\n";
            for (let i = 0; i < this.attendees.length; i++) {
                message+=this.attendees[i].name+"\n";
                if(this.attendees[i].attendeeAllergyCount>0){
                    message+="A kvetkez";
                    if(this.attendees[i].attendeeAllergyCount>1){
                        message+="k";
                    }
                    message+="re igyeksznk figyelni a kedvrt:\n";
                    message+="Nem fogyaszt"+this.attendees[i].allergyText;
                }
                else {
                    message += "Nincs semmilyen kaja ignye"
                }
            }
        }
        message+="\n\n dvzlettel,";
        message+="\nPapp Szonja s Stn dm";
        axios.post('/api/attendee/email',{email:this.attendee.email,msg:message}).then(()=>{
            let toast = this.$toasted.show("Emailt kikldtk");
            toast.goAway(3000);
        }).catch(()=>{
            let toast = this.$toasted.show("Email klds sikertelen, krlek mentsd el az informcikat");
            toast.goAway(30000);
        })
    }
}
</script>

<style scoped>
.my-padding-small{
    margin-top:2em;
}

</style>